.section {
    padding-top: 20px;
    flex-grow: 1;
    flex-shrink: 0;
    flex-basis: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.container {
    align-self: center;
    margin: auto;
}
.text {
    font-size: 100px;
    margin: 0;
    text-align: center;
}